import { Flex, Heading, DateRangePicker, TextField, Form, ButtonGroup, Button} from '@adobe/react-spectrum';
import { useDateFormatter } from '@adobe/react-spectrum';
import { today, getLocalTimeZone, parseDate } from '@internationalized/date';
import React from 'react';
import { queryRequest } from 'connectors/utils';

export function Filter({ hasCheckpoint, data }){
    let [range, setRange] = React.useState({
        start: parseDate('2020-07-03'),
        end: parseDate('2020-07-10')
      });
    let [submitted, setSubmitted] = React.useState(null);
    let [url, setUrl] = React.useState("");
    let [checkpoint, setCheckpoint] = React.useState("");

    let formatter = useDateFormatter({ dateStyle: 'long' });
    let interval, offset;

    const flag = `rum-dashboardFlag`;


    const getQuery = () => {
        queryRequest('rum-dashboard', 'https://helix-pages.anywhere.run/helix-services/run-query@ci5383/', {domainkey: '9e1941e3-76fb-4bcf-89ce-73c0287166de', url, interval: interval ? interval : 30, offset: offset ? offset : 0});
      };

    const makeList = () => {
        if ((Object.hasOwn(window, flag) && window[flag] === true) || !Object.hasOwn(window, flag)) {
            window.setTimeout(makeList, 1000);
          } else if (Object.hasOwn(window, flag) && window[flag] === false) {
            // query complete, hide loading graphic
            data = window.dashboard['rum-dashoard'].results;
        }
    }

    let onSubmit = (e) => {
        // Prevent default browser page refresh.
        e.preventDefault();
    
        // Get form data as an object.
        let data = Object.fromEntries(new FormData(e.currentTarget));
    
        // Submit to your backend API...
        const { start, end, ckpt, url } = data;
        setRange({start, end});
        setUrl(url);
        setCheckpoint(ckpt);
        
        getQuery();
        makeList();
      };

    return (
        <>
            <Flex direction="row" alignItems="center">
                <Heading>FILTERS</Heading>
            </Flex>

            <Form onSubmit={onSubmit} method='get'>
                <DateRangePicker
                    label="Date range"
                    startName="start"
                    endName="end"
                    maxValue={today(getLocalTimeZone())}
                    onChange={setRange}
                    isRequired
                />
                <TextField name='url' label="Url" autoFocus isRequired></TextField>
                {( hasCheckpoint &&
                    <TextField name='ckpt' label="Checkpoint" autoFocus isRequired></TextField>
                )}
                <ButtonGroup>
                    <Button type="submit" variant="primary">Submit</Button>
                    <Button type="reset" variant="secondary">Reset</Button>
                </ButtonGroup>
            </Form>
        </>
    )
}